import React, { Component } from 'react'
import { withRouter, Link } from 'react-router-dom'
import { notFoundRedirects } from 'Shared/constants/Routes'
import { Helmet } from 'react-helmet'

import { PrimaryButton } from 'Shared/components/Button'

import withTranslations from 'Shared/hoc/withTranslations'

import 'Shared/layouts/AppLayout/AppLayout.scss'
import './NotFoundPage.scss'

class NotFoundPage extends Component {
  render () {
    const { t, userRole } = this.props
    return (
      <div className='not-found__container parsing--hidden'>
        <Helmet>
          <meta name='status' content={410} />
        </Helmet>
        <div className='not-found__container-table'>
          <div className='not-found__table-cell'>
            <h1 className='not-found__header'>{t('notFoundPage.header')}</h1>
            <p className='not-found__description'>{t('notFoundPage.description')}</p>
            <Link to={notFoundRedirects[userRole]}>
              <PrimaryButton>{t(`notFoundPage.ctaButton.${userRole}`)}</PrimaryButton>
            </Link>
          </div>
        </div>
      </div>
    )
  }
}

export default withRouter(withTranslations(NotFoundPage))



