import React, { Component } from 'react'
import { withRouter, Link } from 'react-router-dom'
import { notFoundRedirects } from 'Shared/constants/Routes'

import { PrimaryButton } from 'Shared/components/Button'

import withTranslations from 'Shared/hoc/withTranslations'

import 'Shared/layouts/AppLayout/AppLayout.scss'
import './PrivateProfile.scss'

class NotFoundPage extends Component {
  render () {
    const { t, userRole } = this.props

    return (
      <div className='private-profile__container parsing--hidden'>
        <div className='private-profile__container-table'>
          <div className='private-profile__table-cell'>
            <h1 className='private-profile__header'>{t('privateProfilePage.header')}</h1>
            <p className='private-profile__description'>{t('privateProfilePage.description')}</p>
            <Link to={notFoundRedirects[userRole]}>
              <PrimaryButton>{t(`privateProfilePage.ctaButton.${userRole}`)}</PrimaryButton>
            </Link>
          </div>
        </div>
      </div>
    )
  }
}

export default withRouter(withTranslations(NotFoundPage))



